/*-------------------------------------404 PAGE-------------------------------------*/
/*----------------------------------------------------------------------------------*/
.error404 {
    background:#1C1C1C;
}
.error404 main {
    position:absolute;
    left:50%;
    top:50%;
    margin:-60px 0 0 -200px;
    width:400px;
    height:120px;
    text-align:center;
}
.error404 main h1 {
    font-size:40px;
    font-weight:300;
    color:#f1563c;
    margin:0 0 20px;
    text-transform:uppercase;
}
.error404 main h1 i {
    color:#fff;
    font-style:normal;
}
.error404 main p {
    font-size:18px;
    color:#9c9c9c;
    font-family:"Open Sans", sans-serif;
    line-height:1.3;
}
.error404 footer {
    position:absolute;
    left:50%;
    bottom:70px;
    margin:0 0 0 -32px;
    padding:0;
    background: transparent;
}
.error404 footer img {
    display:block;
}
@media only screen and (max-device-width: 1023px) and (orientation: landscape) {
    .page-home .welcome-block {
        margin: 0;
    }
    .page-home .welcome-block .bg-img {
        margin-top: -15%;
    }
    .page-home .welcome-block .splash {
        margin: 0;
    }
    .page-home .welcome-block .splash .bg-img {
        margin: 0;
    }
    .page-home .welcome-block .caption {
        font-size: 3em;
    }
}
@media only screen and (min-device-width: 1024px) and (orientation: landscape) {
    .page-home .welcome-block {
        margin: 0;
    }
    .page-home .welcome-block .bg-img {
        margin-top: -25%;
    }
    .page-home .welcome-block .splash {
        margin: 0 0 15%;
    }
    .page-home .welcome-block .splash .bg-img {
        margin: 0;
    }
    .page-home .welcome-block .caption {
        font-size: 3em;
    }
}
@media only screen and (max-width: 400px) {
    .error404 main {
        position: absolute;
        left: 0;
        top: 50%;
        margin: -60px 0 0;
        width: 100%;
        height: auto;
    }
    .error404 main p {
        font-size: 16px;
    }
}
.page-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 30px;
  width: 100%;
  box-sizing: border-box;

  @media all and (max-width: 992px) {
    padding: 0 39px;
  }

  @media all and (max-width: 540px) {
    padding: 0 20px;
  }
}

.page-apple-business-chat,
.google-rcs {
  .section-title {
    font-size: 48px;
    font-weight: 300;
    line-height: 57px;

    @media all and (max-width: 1280px) {
      font-size: 46px;
    }

    @media all and (max-width: 992px) {
      font-size: 42px;
    }

    @media all and (max-width: 768px) {
      font-size: 40px;
      line-height: 1.2;
    }

    @media all and (max-width: 680px) {
      font-size: 36px;
    }

    @media all and (max-width: 540px) {
      font-size: 32px;
    }

    @media all and (max-width: 380px) {
      font-size: 30px;
    }
  }

  .item-title {
    font-size: 24px;
    font-weight: 500;
    line-height: 28px;
    margin-bottom: 16px;
  }

  .item-description {
    font-size: 16px;
    font-weight: 300;
    line-height: 1.5;

    @media all and (max-width: 540px) {
      font-size: 18px;
      line-height: 1.56;
      letter-spacing: normal;
    }
