    @media all and (max-width: 1440px) {
      padding-top: 80px;
    }

    @media all and (max-width: 1280px) {
      padding-bottom: 70px;
    }

    @media all and (max-width: 768px) {
      padding-top: 60px;
    }

    @media all and (max-width: 540px) {
      padding-bottom: 50px;
    }

    &__title {
      margin-bottom: 60px;
      text-align: center;

      @media all and (max-width: 768px) {
        max-width: 444px;
        margin: 0 auto 50px;
