        margin: 0 auto 60px;
      }
    }
  }

  .bot-form__title {
    text-align: left;
  }
}

.page-apple-business-chat {
  .apple-chat-hero {
    padding-top: 200px;
    text-align: center;

    @media all and (max-width: 1440px) {
      padding-top: 160px;
    }

    @media all and (max-width: 768px) {
      padding-top: 155px;
    }

    @media all and (max-width: 580px) {
      padding-top: 130px;
    }

    &__inner {
      @media all and (max-width: 580px) {
        max-width: 334px;
        margin: 0 auto;
      }
    }

    &__title {
      font-size: 68px;
      font-weight: bold;
      line-height: 1.18;
      padding: 0;
      margin-bottom: 16px;

      @media all and (max-width: 1280px) {
        font-size: 61px;
      }

      @media all and (max-width: 992px) {
        font-size: 54px;
      }

      @media all and (max-width: 768px) {
        font-size: 48px;
        line-height: 1.13;
        margin-bottom: 24px;
      }

      @media all and (max-width: 580px) {
