    user-select: none;
    -webkit-user-select: none;
    position: relative;
    transition: background-color .06s ease;
    -webkit-transition: background-color .06s ease;
}

.no-touch .whoweare-link:hover {
    background-color: #efefef;
}

.no-touch .bg-red:hover {
    background-color: #d54432;
}

