    height: 263px;
}

.whoweare__infographics .full-width {
    width: 100%;
    padding: 0 0 3px 0;
}

.whoweare__infographics .full-width:last-child {
    padding: 3px 0 0 0;
}

.whoweare__infographics .last-item {
    width: 66.6%;
}

.whoweare-link {
    color: inherit;
    text-decoration: none;
    display: block;
    width: 100%;
    height: 100%;
    box-sizing: border-box;
    padding: 15px 25px;
}

.bf-blue {
    color: #7397a4;
}

.bf-red {
    color: #f1563c;
}

.bf-white {
    color: #fff;
}

.bg-red {
    background: #f1563c;
    color: #fff;
}

.big-font {
    display: block;
    font-size: 67px;
    height: 76px;
    line-height: 76px;
    vertical-align: middle;
    opacity: 1;
    z-index: 1;
}

.js-counter {
    transition: all .06s ease;
    -webkit-transition: all .06s ease;
}

.info-startup .big-font {
    font-size: 48px;
}

.pre-count {
    opacity: 0;
    line-height: 76px;
}

.counted {
    opacity: .7;
    font-size: 56px;
    line-height: 76px;
}

.finished {
    opacity: 1;
    font-size: 67px;
    line-height: 76px;
    color: inherit;
}

.finished:after {
    display: inline;
    color: inherit;
    font-size: inherit;
}

.info-professionals .finished:after,
.info-downloads .finished:after {
    content: '+';
}

.info-customers .finished:after {
    content: '%';
}

.info-startup .whoweare-link {
