/*----------------------------------------------------------------------------------*/
/*--------------------------------------PROCESSES-----------------------------------*/
/*----------------------------------------------------------------------------------*/
.processes-description {
  max-width: 600px;
  width: auto;
  padding: 30px;
  margin: -20px auto 30px;
  text-align: center;
}
.process-icon__holder {
  width: 140px;
  height: 140px;
  margin: 27px ;
  display: block;
  background: #f2f2f2;
  transform: rotate(-45deg);
  -webkit-transform: rotate(-45deg);
  animation-fill-mode: backwards;
  -webkit-animation-fill-mode: backwards;
  transition: transform .5s ease-in-out;
  -webkit-transition: transform .5s ease-in-out;
  position: relative;
}
.process-icon {
  height: 72px;
  width: 52px;
  line-height: 72px;
  vertical-align: middle;
  fill: #f1563c;
  transform: rotate(45deg);
  -webkit-transform: rotate(45deg);
  position: absolute;
  top: 50%;
  left: 50%;
  margin-left: -26px;
  margin-top: -36px;
  transition: transform .5s ease-in-out;
  -webkit-transition: transform .5s ease-in-out;
  animation-fill-mode: backwards;
  -webkit-animation-fill-mode: backwards;
}
.process-icon img{
  height: auto;
  width: auto;
  max-height: 100%;
  max-width: 100%;
  display: inline-block;
  vertical-align: middle;
}
.processes-list__item.opened {
  z-index: 5;
}
.processes-list__item.opened .process-icon__holder {
  transform: rotate(0deg);
  -webkit-transform: rotate(0deg);
  transition: transform .5s ease-in-out;
  -webkit-transition: transform .5s ease-in-out;
  animation-fill-mode: backwards;
  -webkit-animation-fill-mode: backwards;
}
.processes-item__content {
  position: absolute;
  left: 0;
  top: 100%;
  padding: 0 27px;
  width: 700px;
  background: #fff;
  text-align: left;
  font-size: 20px;
  line-height: 1.5;
  height: 0;
  overflow: hidden;
  animation-fill-mode: backwards;
  -webkit-animation-fill-mode: backwards;
}
.processes-list {
  display: block;
  margin: 60px auto 0;
  padding-bottom: 265px ;
  overflow: hidden;
  text-align: center;
}
.processes-list__item {
  display: inline-block;
  position: relative;
  vertical-align: top;
  -webkit-backface-visibility:  hidden;
  -webkit-tap-highlight-color:  transparent;
}
.processes-list__item.opened .processes-item__content {
  height: 200px;
  padding: 27px;
  transition: all .5s ease-in-out;
  -webkit-transition: all .5s ease-in-out;
}
.processes-header__wrapper {
  margin: 35px 27px 0;
  height: 75px;
  width: 140px;
  position: relative;
}
.processes-item__header {
  position: absolute;
  color: #f1563c;
  font-size: 20px;
  text-align: left;
  line-height: 40px;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  -webkit-transform: translateX(-50%);
  right: inherit;
  margin: 0;
  padding-bottom: 35px;
  width: auto;
  max-width: 140px;
  font-weight: 400;
  vertical-align: middle;
  transition: all .3s .2s linear, font-size 0.5s ease;
  -webkit-transition: all .3s .2s linear, font-size 0.5s ease;
  animation-fill-mode: backwards;
  -webkit-animation-fill-mode: backwards;
}
.processes-list__item:nth-child(n+4) .processes-item__header{
  left: inherit;
  right: 50%;
  transform: translateX(50%);
  -webkit-transform: translateX(50%);
  text-align: right;
}
.processes-item__header:after {
  content: '';
  display: block;
  position: absolute;
  height: 4px;
  width:  65px;
  bottom: 5px;
  left: 0;
  transform: scaleX(0);
  -webkit-transform: scaleX(0);
  transition: transform .5s ease-in-out;
  -webkit-transition: transform .5s ease-in-out;
  transform-origin: left;
  animation-fill-mode: backwards;
  -webkit-animation-fill-mode: backwards;
}
.processes-list__item:nth-child(n+4) .processes-item__header:after {
  right: 0;
  left: inherit;
  -webkit-transform-origin: right;
  transform-origin: right;
}
.processes-list__item.opened .processes-item__header:after {
  background-color: #f1563c;
  transform: scaleX(1);
  -webkit-transform: scaleX(1);
}
.processes-list__item.opened .processes-item__header {
  transform: translateX(0%);
  -webkit-transform: translateX(0%);
  left: 0;
  color: #333;
  font-size: 32px;
  font-weight: 700;
  transition: all .3s linear, font-size 0.5s ease;
  -webkit-transition: all .3s linear, font-size 0.5s ease;

}
.processes-list__item.opened .process-icon{
  transition: all .5s ease-in-out;
  -webkit-transition: all .5s ease-in-out;
}
.processes-list__item:nth-child(n+4) .processes-item__content {
  text-align: right;
  right: 0;
  left: inherit;
}
.processes-list__item:nth-child(n+4).opened  .processes-item__header{
  right: 0;
  left: inherit;
}
.processes-list__item.development.opened  .processes-item__header{
  min-width: 180px;
}
