.webinars-page-content {

  * {
    box-sizing: border-box;
  }
}

.webinars-container {
  padding-bottom: 75px;
}

.webinar-link {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}

.post-link {
  font-weight: 500;
  font-size: 18px;
  line-height: 24px;
  text-transform: capitalize;
  text-decoration: none;
  display: flex;
  align-items: center;
  position: relative;
  z-index: 2;

  &:hover {
    .arrow-left {
      transform: translateX(5px);
    }
  }

  .arrow-left {
    width: 10px;
    height: 24px;
    margin-left: 7px;
    margin-top: 2px;
    transition: .4s;
  }
}

.category-list {
  display: flex;
}

.category-item {

  &:last-of-type {
    &:after {
      display: none;
    }
  }
}

.upcoming-webinars-wrap {
  width: 100%;
  margin: 0 0 40px;
  position: relative;
  overflow: hidden;
  display: flex;
  background-color: #0A1A34;

  //@media all and (min-width: 541px) {
  //  margin: 0 0 40px;
  //}
  //
  //@media all and (min-width: 641px) {
  //  width: 100%;
  //  margin: 0 0 40px;
  //}


  @media all and (min-width: 1025px) {
    max-width: 1140px;
    margin-left: auto;
    margin-right: auto;
  }

  .owl-stage {
    //display: flex;
    display: flex;
    flex-direction: row;
  }

  .owl-stage::after {
    display: none;
  }

  .owl-item {
    float: none;
    height: inherit;
  }

  .owl-dots {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    bottom: 24px;
    padding: 0 20px;
  }

  button.owl-dot {
    width: 8px;
    height: 8px;
    border-radius: 50%;
    margin: 0 4px;
    background: #C4C4C4;
    opacity: 0.5;
    border: none !important;
    transition: .4s;

    &.active {
      opacity: 1;
    }
  }

  &.owl-carousel {

    .owl-nav {
      padding: 0;
      top: 50%;

      @media all and (max-width: 640px) {
        display: none;
      }

      button.owl-prev,
      button.owl-next {
        height: 40px;
        width: 40px;
      }

      button.owl-prev:after,
      button.owl-next:after, {
        height: 12px;
        width: 12px;
        border-right-width: 2px;
        border-bottom-width: 2px;
        opacity: 0.3;
      }
    }
  }
}

.webinar-bg-wrapper {
  position: absolute;
  height: 100%;
  width: 100%;
  right: 0;
  top: 0;
  z-index: -1;

  .webinar-bg-image {
    height: 100%;
    min-height: 100%;
    min-width: 100%;
    width: 100%;
    -o-object-fit: cover;
    object-fit: cover;
    object-position: center right;
    margin: auto;
    display: block;

    @media all and (max-width: 500px) {
      object-position: center left;
    }
  }
}

.upcoming-webinar {
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center right;
  padding: 17px 52px 32px 20px;
  color: #ffffff;
  position: relative;
  height: 100%;
  min-height: 340px;
  box-sizing: border-box;
  width: 100%;

  &:not(:first-of-type) {
    position: absolute;
  }

  @media all and (min-width: 641px) {
    padding: 40px;
    min-height: 350px;
  }

  @media all and (min-width: 1025px) {
    min-height: 450px;
  }


  @media all and (max-width: 460px) {
    background-position: center left;
  }

  .post-link {
    color: inherit;

    path {
      fill: #ffffff;
    }
  }

  .post-info {
    padding: 0;
  }
}

.upcoming-webinar__info {
  font-size: 14px;
  line-height: 24px;
  display: flex;
  margin-bottom: 72px;
  flex-wrap: wrap;

  @media all and (min-width: 641px) {
    margin-bottom: 60px;
  }

  @media all and (min-width: 1025px) {
    margin-bottom: 138px;
  }
}

.upcoming-webinar__info-item {
  margin-right: 9px;
  position: relative;

  &:after {
    content: '';
    display: inline-block;
    width: 1px;
    height: 15px;
    background-color: #ffffff;
    position: absolute;
    top: 5px;
    right: -5px;
  }
}

.upcoming-webinar__label {
  display: inline-block;
  padding: 6px 20px 4px;
  background-color: #F1563C;
  font-weight: 500;
  font-size: 14px;
  line-height: 22px;
  text-align: center;
  letter-spacing: 1px;
  text-transform: uppercase;
  color: #FFFFFF;
  margin-bottom: 15px;

  @media all and (min-width: 641px) {
    margin-bottom: 20px;
  }

  @media all and (min-width: 1025px) {
    margin-bottom: 16px;
  }
}

.upcoming-webinar__title {
  font-weight: 500;
  font-size: 24px;
  line-height: 32px;
  margin-bottom: 16px;
  transition: .3s;
  position: relative;
  z-index: 2;

  &:hover {
