.coming-soon-video {
  min-height: 300px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  margin-top: -80px;
}

.coming-soon-video__text {
  margin: 0;
  font-size: 28px;
  line-height: 1.25;
  max-width: 200px;
}

.play-video-icon {
  width: 68px;
