  //popup with video
.webinar-video-popup {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: 9999;
  background-color: rgba(0, 0, 0, 0.3);
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 40px 80px;
  overflow: scroll;
  transition: .4s;
  opacity: 0;
  visibility: hidden;

  &.show {
    opacity: 1;
    visibility: visible;
  }


  @media all and (max-width: 992px) {
    padding: 40px;
  }

  @media all and (max-width: 540px) {
    padding: 20px;
  }

  .video-wrap {
    width: 80%;
    max-width: 1145px;
    position: relative;

    @media all and (max-width: 1340px) {
      width: 90%;
    }

    @media all and (max-width: 992px) {
      width: 95%;
    }

    @media all and (max-width: 680px) {
      width: 100%;
    }
  }

  .video-close-button {
    padding: 0;
    background: none;
    border: none;
    width: 24px;
    height: 24px;
    position: absolute;
    top: 0;
    right: -39px;
    cursor: pointer;
    transition: .3s;
    outline: none;

    @media all and (max-width: 680px) {
      top: -30px;
      right: 0;
    }

    &:hover {
      .close-icon rect {
        fill: #f1563c;
      }
    }

    .close-icon {
      width: 100%;
      height: 100%;
      transition: .2s;

      rect {
        transition: .4s;
      }
    }
  }

  .video {
    position: relative;
    width: 100%;
    max-width: 100%;
    height: 0;
    padding-bottom: 56.25%;
    background-color: #ffffff;
  }

  .video__embed {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border: none;
  }
}
